html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow-long {
  width: 14.49em;
  height: 1em;
  fill: currentColor;
}

.icon-arrow-right {
  width: 2.92em;
  height: 1em;
  fill: currentColor;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

.icon-phone {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

body {
  background: #FFF;
  line-height: 1.4;
  font-size: 16px;
}

body a {
  text-decoration: none;
}

.title-lg {
  font-size: 60px;
  letter-spacing: .2px;
  font-weight: 300;
}

.btn {
  padding: 0 40px;
  height: 60px;
  text-transform: uppercase;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  font-size: 14px;
  transition: background-color .3s ease, color .3s ease;
}

.btn_white {
  color: #fff;
}

.btn_outline {
  background-color: transparent;
  border: 1px solid #f6f6f6;
}

.no-touch .btn_outline:hover {
  background-color: #fff;
  color: #333333;
}

.no-touch .btn_outline:hover .btn__icon {
  transform: translateX(5px);
}

.no-touch .btn_outline:hover .btn__icon_green {
  color: #333333;
}

.btn__icon {
  font-size: 7px;
  margin-left: 20px;
  transition: color .3s ease, transform .3s ease;
}

.btn__icon svg {
  display: block;
  max-width: 100%;
}

.btn__icon_green {
  color: #0d9838;
}

.inner {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px 0 115px;
}

.block {
  display: flex;
}

.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100px;
  height: 100%;
  z-index: 100;
  min-height: 430px;
}

.header.is-active {
  width: 100%;
  height: 100px;
}

.header__wrap {
  position: absolute;
  top: 100px;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(26, 29, 36, 0.6);
  transition: background-color .3s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header.is-active .header__wrap {
  top: 0;
  background-color: #1a1d24;
}

.header__contacts {
  transform-origin: center;
  transform: rotate(-90deg);
  display: flex;
  align-items: center;
  justify-content: center;
}

.header.is-active .header__contacts {
  transform: translateY(-50%);
  min-height: 0;
}

.header__contacts a {
  display: block;
  flex-shrink: 0;
}

.header__contacts a:not(:first-child) {
  margin-left: 30px;
}

.phone,
.email {
  font-size: 22px;
  color: #fff;
}

.burger {
  position: relative;
  width: 100%;
  height: 100px;
  background-color: rgba(9, 10, 15, 0.7);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color .5s ease;
  z-index: 1;
}

.no-touch .burger:hover .burger__line {
  width: 100%;
}

.header.is-active .burger {
  width: 100px;
  height: 100%;
}

.burger__icon {
  display: block;
  width: 27px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.burger__line {
  display: block;
  width: 100%;
  height: 2px;
  margin: 6px 0;
  background-color: #fff;
  transform-origin: center;
  transition: width .3s, transform .3s, margin .3s;
}

.burger.is-active .burger__line {
  width: 100%;
  margin: 0;
  height: 0;
}

.burger__line_md {
  width: 73%;
  margin: 0;
}

.burger.is-active .burger__line_md {
  width: 100%;
  transform: rotate(45deg);
  transition-delay: .3s;
  height: 2px;
}

.burger__line_sm {
  width: 42%;
  margin: 0;
}

.burger.is-active .burger__line_sm {
  width: 100%;
  transform: rotate(-45deg);
  transition-delay: .3s;
  height: 2px;
  margin-top: -2px;
}

.burger-menu {
  position: fixed;
  left: 100px;
  top: 0;
  bottom: 0;
  width: 320px;
  background-color: #1a1d24;
  border-left: 1px solid #2d2d2d;
  transition: transform .3s ease, opacity .3s ease;
  transform: translateX(-100%);
  opacity: 0;
}

.burger-menu.is-open {
  transform: translateX(0);
  opacity: 1;
}

.burger-menu__inner {
  position: relative;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.burger-menu__item {
  position: relative;
  padding-left: 40px;
}

.burger-menu__item:not(:first-child) {
  margin-top: 20px;
}

.burger-menu__item.is-active:before {
  content: '';
  position: absolute;
  width: 20px;
  height: 2px;
  background-color: #0d9838;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}

.burger-menu__link {
  color: #fff;
  font-size: 18px;
  transition: color .3s;
}

.no-touch .burger-menu__link:hover {
  color: #0d9838;
}

.hero {
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
}

.hero__content {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  height: 100%;
  color: #fff;
}

.hero__contacts {
  position: absolute;
  left: 0;
  top: 0;
}

.hero__link {
  display: flex;
  align-items: center;
  height: 100px;
  color: #fff;
  font-size: 20px;
}

.hero__link-icon {
  font-size: 25px;
}

.hero__link-icon svg {
  display: block;
  max-width: 100%;
}

.hero__link-text {
  margin-left: 30px;
}

.hero__left {
  flex: 0 0 58.33333%;
  max-width: 58.33333%;
}

.hero__right {
  flex: 0 0 33.33333%;
  max-width: 33.33333%;
  margin-left: 20px;
  height: 100%;
}

.hero__subtitle {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.2);
  text-transform: uppercase;
  letter-spacing: 2.59px;
}

.hero__title:not(:first-child) {
  margin-top: 30px;
}

.hero__text {
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  margin: 40px 0 64px;
}

.hero-menu {
  height: 100%;
}

.hero-menu__item {
  position: relative;
  height: 33.33333%;
  display: flex;
  align-items: center;
  transition: height .3s ease, background-color .3s ease, border-color .3s ease;
  border: 1px solid #2d2d2d;
  color: #fff;
}

.no-touch .hero-menu__item:hover {
  background-color: rgba(26, 29, 36, 0.7);
  border-color: rgba(26, 29, 36, 0.7);
}

.no-touch .hero-menu__item:hover .hero-menu__continue {
  max-height: 80px;
}

.no-touch .hero-menu__item:hover .hero-menu__more {
  opacity: 1;
  transform: translateX(0);
}

.hero-menu__item:first-child, .hero-menu__item:last-child {
  border-top: none;
  border-bottom: none;
}

.hero-menu__wrap {
  padding: 20px 40px;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.hero-menu__name, .hero-menu__continue, .hero-menu__more {
  display: block;
}

.hero-menu__continue {
  height: 80px;
  max-height: 0;
  position: relative;
  overflow: hidden;
  transition: max-height .3s ease;
}

.hero-menu__more {
  position: absolute;
  left: 0;
  bottom: 0;
  opacity: 0;
  transform: translateX(-40px);
  transition: transform .3s ease, opacity .3s ease;
  transition-delay: .2s;
  color: rgba(255, 255, 255, 0.3);
  font-size: 12px;
}

.arrow-more {
  display: flex;
}

.no-touch .arrow-more:hover .arrow-more__icon-inner {
  max-width: 70px;
}

.arrow-more__icon {
  position: relative;
  max-width: 84px;
  width: 84px;
  margin-left: 20px;
}

.arrow-more__icon .icon {
  display: block;
}

.arrow-more__icon-inner {
  position: absolute;
  right: 0;
  max-width: 84px;
  width: 100%;
  overflow: hidden;
  height: 100%;
  transition: max-width .5s ease;
}

.arrow-more__icon-wrap {
  max-width: 84px;
  width: 84px;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
}
