.header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100px;
    height: 100%;
    z-index: 100;
    min-height: 430px;
    &.is-active {
        width: 100%;
        height: 100px; }
    &__wrap {
        position: absolute;
        top: 100px;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba($menu-bg,.6);
        transition: background-color .3s;
        // z-index: -1
        display: flex;
        align-items: center;
        justify-content: center;
        .header.is-active & {
            top: 0;
            background-color: $menu-bg; } }
    &__contacts {
        // height: 20px
        // text-align: center
        // position: absolute
        transform-origin: center;
        transform: rotate(-90deg); //translateY(-50%)
        // width: 50vh
        display: flex;
        align-items: center;
        justify-content: center;
        // top: 50%
        // left: 0
        // right: 0
        // bottom: 0
        // min-height: 320px
        .header.is-active & {
            transform: translateY(-50%);
            min-height: 0; }
        // flex-wrap: wrap
        a {
            display: block;
            flex-shrink: 0;
            &:not(:first-child) {
                margin-left: 30px; } } } }

.phone,
.email {
    font-size: 22px;
    color: $white; }
